@import url("https://fonts.googleapis.com/css2?family=Jost:wght@200;400;700&display=swap");
*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	border: 0;
	box-sizing: border-box;
}
html,
body {
	background-color: #1c1a1b;
	color: #fff;
	font-family: "Jost", sans-serif;
	font-weight: 400;
	font-size: 62.5%;
	line-height: 1.7;
	text-rendering: optimizeLegibility;
	overflow-x: hidden;
	height: 100vh;
	overflow-y: hidden;
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Safari */
	-khtml-user-select: none; /* Konqueror HTML */
	-moz-user-select: none; /* Old versions of Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}
input:focus,
select:focus,
textarea:focus,
button:focus {
	outline: none;
}
button {
	border: none;
	margin: 0;
	padding: 0;
	width: auto;
	overflow: visible;
	text-align: inherit;
	border-radius: 0;
	background: transparent;
	color: inherit;
	font: inherit;
	line-height: normal;
	-webkit-font-smoothing: inherit;
	-moz-osx-font-smoothing: inherit;
	-webkit-appearance: none;
}
li {
	list-style: none;
}
a {
	color: inherit;
	text-decoration: none;
}
button::-moz-focus-inner {
	border: 0;
	padding: 0;
}
.container {
	width: 90%;
	margin: 0 auto;
}
nav {
	img {
		width: 1.5rem;
		cursor: pointer;
	}
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 1rem 0;
	.logo {
		font-size: 4rem;
	}
	.navigation-links {
		display: flex;
		font-size: 1.5rem;
		color: gray;
		.active-link {
			color: #fff;
		}
		flex: 2;
		justify-content: flex-end;
		margin-right: 4rem;
		li {
			transition: all 0.4s;

			&:not(:last-of-type) {
				margin-right: 4rem;
			}
			&:hover {
				color: #fff;
			}
		}
	}
	.interactions {
		img:not(:last-of-type) {
			margin-right: 1rem;
		}
	}
}
.col-3 {
	display: grid;

	grid-template-columns: 1fr 2fr 1fr;
	justify-items: center;
}
.pink-circle {
	background-color: #d79c9b;
	width: 45rem;
	height: 45rem;
	border-radius: 50%;
	box-shadow: 0px 0px 20px 4px #e4c0c1;
	transition: all 0.4s;

	&.active-circle {
		background-color: #ffc7c6;
		transform: scale(0.9);
	}
}
main {
	margin-top: 4rem;
}
h1 {
	font-size: 7rem;
	line-height: 70px;
}
p {
	margin: 2rem 0;
	color: gray;
	font-size: 1.5rem;
}
.image-selector {
	// margin: 1rem 0;
	display: flex;
	span:first-of-type {
		font-size: 8rem;
		font-weight: bolder;
	}
	span:last-of-type {
		font-size: 3rem;
		// font-weight: bold;
		margin-left: 2rem;
		margin-top: 2rem;
	}
}
.nav-arrows {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	img {
		width: 4rem;
	}
	div {
		width: 8rem;
		height: 8rem;
		border: 1px solid #fff;
		display: flex;
		justify-self: flex-start;
		align-items: center;
		justify-content: center;
		border-radius: 50%;
		// cursor: pointer;
		position: relative;

		svg {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			overflow: visible;
			cursor: pointer;
			fill: none;
			stroke: #fff;
			stroke-width: 5;
			circle {
				stroke-dashoffset: 470;
				stroke-dasharray: 470;
				transition: all 0.4s;
			}
		}
		&:hover {
			svg {
				circle {
					stroke-dashoffset: 281;
					stroke-dasharray: 532;
				}
			}
		}
		&:last-of-type {
			margin-left: 5rem;
		}
	}
}
.col:nth-of-type(2) {
	align-self: start;

	position: relative;

	img {
		position: absolute;

		width: 35rem;
		top: 0;
		transition: all 0.4s;
		left: 50%;
		transform: translateX(-50%) translateY(-5rem);
	}
}
.col:nth-of-type(3) {
	display: flex;
	flex-direction: column;
	justify-content: center;
	img {
		width: 2rem;
	}
}
.about-product {
	position: relative;
	// width: 130%;
	height: 7rem;
	h4 {
		font-size: 1.5rem;
	}
	p {
		margin: 0.5rem 0;
		font-size: 1.7rem;
		color: #fff;
		font-weight: lighter;
	}
	.content {
		display: flex;
		// position: absolute;
		width: 100%;
		justify-content: space-between;
	}
	// div:first-of-type {
	// 	width: 100%;
	// }
	margin: 2rem 0;
}
h3 {
	font-size: 3rem;
}
.price {
	h2 {
		font-size: 4rem;
	}
	margin: 2rem 0;
}
.about-company {
	p {
		margin: 0.2rem 0;
	}
}
.read-more {
	margin: 2rem 0;
	font-size: 1.5rem;
	align-self: flex-end;
	cursor: pointer;
}
.icons {
	align-self: flex-end;
	margin-top: 2rem;
	img {
		cursor: pointer;
	}
	img:not(:last-of-type) {
		margin-right: 5rem;
	}
	.facebook {
		width: 1.5rem !important;
	}
}
